
* {font-family:'Open Sans','Arial','Helvetica','Trebuchet MS','sans-serif' !important; -webkit-tap-highlight-color: rgba(0,0,0,0);
	/* cursor: default; */
  	/* line-height: 18px; */ 
  }

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

label {
  display: block;
  padding: 10px 0;
  color: #404040;
  font-size: 15px;
}

input[type="text"], input[type="tel"], select {
	-webkit-appearance: none;
	-moz-appearance: none;

	margin-bottom: 8px;
	background-color: #fff;
	border: 1px solid #d0d0d0;
	border-radius: 0;
	font-size: 14px;
	height: 44px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

input[type="text"], input[type="tel"] {
	padding: 2px 10px 1px;
}

select {
	padding: 5px 10px 4px;
}

@media all and (-ms-high-contrast:none)
{
	input[type="text"], input[type="tel"] {
		padding: 10px;
	}
	select::-ms-expand {
		display: none;
	}
}

@media screen and (min-width: 1024px) {
	input[type="text"], input[type="tel"], select {
		height: 40px;
	}
}

select.gray {
	color: #c0c0c0;
}

option, select.gray option {
	color: #000;
	/*padding: 4px;
	font-size: 15px;
	line-height: 24px;*/
}

select option:disabled, select option.gray {
	color: #c0c0c0;
}

select {
}

button {
	padding: 16px 18px 16px 18px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	color: #fff;
	background-color: #a00000;
	border: 1px solid #a00000;
 	border-radius: 0;
	-webkit-appearance: none;
	-moz-appearance: none;
	     appearance: none;
	font-size: 15px;
	min-width: 90px;
}

button:hover {
	background-color: #600000;
}

label, input[type="text"], select {
	width: 100%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

input:focus, select:focus { border-color: blue; }

a {
	font-size: 14px;
	text-decoration: underline;
	cursor: default;
	padding: 2px 6px 4px;
	border-radius: 2px;
}

a:hover {
	background-color: #e0e0e0;
	color: #000080;
}

a:active {
	background-color: #606060;
	color: #fff;
}



