.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 56px;
  width: 72px;
}

.App-header {
  background-color: #222;
  /*height: 150px;*/
  padding: 0;
  color: white;
}

.App-title {
  font-size: 1.5em;
  display: inline-block;
  vertical-align: top;
  margin: 10px 0;
  font-weight: normal;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.logo-span {
  display: inline-block;
  width: 72px;
  height: 56px;
  vertical-align: top;
  text-align: left;
  margin-right: -6px;
}

/** right aligned strip of buttons */

.button-bar {
  text-align: right;
  padding: 8px 0;
  line-height: 66px; /* IE11 doesn't like this */
}

.button-bar button {
  margin-left: 10px;
  line-height: 16px;
}

/** form container */

.form-fields {
  width: 600px;
  display: inline-block;
  max-width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 12px;
  text-align: left;
}

.brand {
    border-left: 4px solid #f02020;
    color: #444;
    font-weight: normal;
    padding-left: 7px;
    font-size: 26px;
    height: 23px;
    line-height: 24px;
}

.white {
  color: #404040;
  background-color: #f0f0f0;
  border: 1px solid #404040;
}

.white:hover {
  color: #fff;
  background-color: #404040;
  border: 1px solid #404040;
}

/** error on input field */

.inline-error {
  font-size: 15px;
  color: #88000a;
  margin: 0 0 8px 0;
}

.error-icon {
  font-size: 24px;
  display: inline-block;
  vertical-align: -2px;
  margin-top: -8px;
}

/* ----------- DATE FIELDS ------------- */

input[type="text"].date-2 {
  width: 4rem;
  margin-right: 8px;
}

input[type="text"].date-4 {
  width: 6rem;
  margin-right: 10px;
}

/* input fields */

.invlabel {
  color: #88000a;
}

.inval, input[type="text"].inval {
  color: #333;
  border-color: #88000a;
  background-color: #f9f3f3;
}

.inval:focus, input[type="text"].inval:focus {
  color: #000;
  background-color: #fff;
}

/* POPUP */

.popup-outer {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  text-align: center;
  background-color: rgba(0,0,0,0.25);
  /* filter: blur(1px); */
  z-index: 2;
}

.popup-inner {
  position: relative;
  background-color: #fff;
  color: #000;
  padding: 8px 24px;
  border: 1px solid #000;
  text-align: left;
  display: inline-block;
  z-index: 3;

  top: 48px;      /** size, pos, todo: media queries */
  width: 400px;
  max-width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

/** ---------- ToolTip ----------- */

.tooltip {
  position: relative;
  margin-left: 6px;
  cursor: default;
  padding-right: 24px;
}

.tip-button {
  position: absolute;
  display: inline-block;
  color: #fff;
  background-color: #444;
  padding: 0;
  border: 0;
  text-align: center;
  top: -1px;
  font-weight: bold;
  border-radius: 11px;
  width: 22px;
  min-width: 22px;
  height: 22px;
}

.tip-box {
  position: absolute;
  display: inline-block;
  padding: 8px;
  background-color: #2d2a5d;
  color: #fff;
  font-size: 14px;
  border: 1px solid #000;
  min-width: 240px;
  top: 36px;
  left: -110px;
  border-radius: 4px;
}

.tip-box:before {
  position: absolute;
  width: 0;
  height: 0;
  content: '';
  border: 12px solid transparent;
  border-width: 14px 9px;
  border-bottom-color: #2d2a5d;
  top: -28px;
  left: 110px;
  /*z-index: 22;*/
}

.tip-left {
  left: -20px;
}

.tip-left:before {
  left: 20px;
}

.tip-right {
  left: -225px;
}

.tip-right:before {
  left: 225px;
}

.tip-button:hover {
  background-color: #962f0a;
}

.tip-button:focus { 
  background-color: blue;
  outline: none;
}

.tip-box:focus {
  outline: none;
}

/* InfoBox */

.info-grid {
  background-color: #e0e0e0;
  padding: 8px 0;
}

.info-grid dt, .info-grid dd {
  width: 50%;
  display: inline-block;
  vertical-align: top;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 6px 12px;
  margin: 0;
  color: #333;
}

.info-grid dt {
  color: #5c5c5c;
  font-size: 0.875rem;
}

@media screen and (max-width: 480px) {
  .info-grid dt, .info-grid dd {
    width: 100%;
  }  
  .info-grid dt {
    padding-bottom: 0;
  }
  .info-grid dd {
    padding-left: 24px;
  }  
}




button.radio, button.radio-check {
  width: 26px;
  height: 26px;
  padding: 0;
  min-width: 26px;
  vertical-align: -7px;
  border: 1px solid #ababab;
  background-color: #fff;
  margin-right: 4px;
  position: relative;

  /*border-radius: 13px; /* like radio */
}

button.radio:hover, button.radio-check:hover {
  background-color: #f0f0f8;
}

button.radio span {
  display: none;
}

button.radio-check span {
  background-color: #4d494d;
  display: inline-block;
  width: 12px;
  height: 12px;
  position: absolute;
  left: 6px;
  top: 6px;
  pointer-events: none;

  /*border-radius: 6px; /* like radio */
}



