
.currencyPickDiv {
    position: relative;
}

.currencyPick {
    width: 86px;
    margin-right: 10px;
    color: #fff;
    font-size: 16px;
    vertical-align: top;
}

.currencyPick:focus::-ms-value {
    color: #fff;
    background: #fff;
}

.currencyPick:focus {
    /*color: #fff;
    background-color: #2d2a5d;*/
    z-index: 2;
    position: relative;
}

.currencyPick:focus ~ .currencyLozenge {
    background-color: #374180;
}

.currencyPick option {
    font-size: 14px;
    line-height: 18px;
    background-color: #666;
    color: #f0f0f0;
}

.currencyInputDiv {
    display: inline-block;
    position: absolute;
    right: 0;
    left: 85px;
}

input[type="tel"].currencyInput {
    /* width: 140px; */
    width: 100%;
    font-size: 16px;
    vertical-align: top;
}

.currencyLozenge {
    position: absolute;
    top: 13px;
    left: 11px;
    width: 32px;
    height: 17px;
    padding: 1px;
    text-align: center;
    border-radius: 2px;
    background-color: #808080;
    color: #e0e0e0;
    font-size: 12px;
    font-weight: 600;
    pointer-events: none;
    z-index: 3;
}

.currencyPickDiv svg {
    position: absolute;
    top: 13px;
    left: 58px;
    width: 12px;
    height: 12px;
    pointer-events: none;
    stroke: #808080;
    fill: none;
    z-index: 3;
}

@media screen and (min-width: 1024px) {
    .currencyLozenge {
        top: 11px;
    }
}




